  .container {
    width: 768px;
    padding-left: 32px;
    padding-right: 32px;
  }
}

@media screen and (min-width: 1200px) {
  .container {
    width: 1200px;
  }
}

@media screen and (min-width: 1280px) {
  .container {
    width: 1280px;
  }
}

.btn {
  text-align: center;
  min-width: 150px;
  height: 44px;
  border: 1px solid var(--primary-bg-color);
  text-transform: uppercase;
  color: var(--primary-bg-color);
  background-color: rgba(0, 0, 0, 0);
  border-radius: 5px;
  justify-content: center;
  align-items: center;
  padding: 13px 0;
  font-size: 12px;
  font-weight: 500;
  line-height: 1.34;
